.header {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 20px;
    position: fixed;
    top: 0;
    /* background-color: white; */
    width: 100%;
}

.changeMode {
    /* background: transparent; */
    border: none;
    font-family: 'Nunito Sans';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    margin-right: 30px;
    cursor: pointer;
    display: flex;
    align-items: center;
}
@media screen and (max-width: 500px) {
    .header{
        width: 100%;
        font-size: 10px;
    }
}